import React, { Component } from 'react'
import './css.css';
import utils from '../../library'

class Gallery extends Component {
state={link3d:""}

componentDidMount(){
    this.MetaList()
}

    async MetaList(){
        let pioneers=[
        "0x6692bf36afe9d3954f5c92b428f9068e8ee59b02",
        "0xdA1Ec8F2Fb47e905079663bCEA69f1a2B010f2D3"
]
        let link3d
        let i=1
        for(let x=0;x<2;x++){
         
        const uri = await utils.metadata(pioneers[x])     
        alert(uri)  
        const itemMeta = await utils.itemMetaJSON("ipfs://ipfs/"+uri) 
        alert(itemMeta)
        
        if(!link3d){link3d="&cx"+i+"="+utils.uri2url(itemMeta["image"])}else{link3d+="&cx"+i+"="+utils.uri2url(itemMeta["image"])}
        
        alert("link"+link3d)
        i++;
        }
        alert("link2"+link3d)
        await this.setState({link3d:link3d})
      }
    render(){
        let file_Q="QmYm26D2GeAFTirs5NTtuLKsBtxD7n4QinD5fAt7qmw5u6"
        return ( 
        <div>
        <iframe id="gallerydisplay" width="400" height="400" scrolling="no" src={"/three/gallery.html?vox="+file_Q+this.state.link3d}></iframe>
                
      
        </div>
        );
    }


}




export default Gallery